.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;

    padding: 0 2px;
    margin-right: 4px;

    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }
}

.stars--1 :nth-child(1),
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 .stars__star {
  background-image: url(../images/star-active.svg);
}
